body {
  text-align: center;
  background-color: slategrey;
  height: 100vh;
}
#button-grid {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-template-rows: repeat(3, 100px);
  grid-gap: 20px;
  justify-content: center;
}
.drum-pad {
  background-color: lime;
}
.App-logo {
  animation: App-logo-spin infinite 69s linear;
  height: 20vmin;
}

h1 {
  text-decoration: underline;
}

#display {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 5vh;
  margin-top: 5vmin;
}

#banner {
  margin: 0;
  padding: 0;
  border: 2px solid orange;
  color: black;
  width: 50vw;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
